export function maskPhoneNumber(phone = "") {
  const phoneNumber = phone.trim();

  if (phoneNumber === "+" || phoneNumber === "+7" || phoneNumber === "+7 (") {
    return phoneNumber;
  }

  if (phoneNumber === "7" || phoneNumber === "8") {
    return "+7";
  }

  const formattedPhone = phoneNumber.replace(/\D/g, "").slice(0, 12);

  const matchPhone = formattedPhone
    .replace(/\D/g, "")
    .replace(/^\+?([78])?/, "")
    .match(/^(\d{0,3})(\d{0,3})(\d{0,2})(\d{0,2})/);

  if (matchPhone) {
    if (matchPhone[1] && !matchPhone[2]) {
      return `+7 (${matchPhone[1]}`;
    }

    if (matchPhone[2]) {
      const number = matchPhone.slice(2, 5).filter(Boolean).join("-");

      return `+7 (${matchPhone[1]}) ${number}`;
    }

    return ``;
  }

  return phoneNumber;
}

export function maskDivisionCode(value = "") {
  const code = value.trim();

  const matchCode = code.replace(/\D/g, "").match(/^(\d{0,3})(\d{0,3})$/);

  if (matchCode) {
    return [matchCode[1], matchCode[2] && "-", matchCode[2]]
      .filter(Boolean)
      .join("");
  }

  return "";
}
